.root {}

.list {
    display: grid;
    grid-row-gap: 0.5em;
    list-style: none;
    padding: 0.5em 1em;
}

.delimiter {
    font-size: var(--font-size-S);
    opacity: 0.5;
}

.delimiter:before, .delimiter:after {
    content: ' ------------------------- ';
}

.branch {
    font-weight: bold;
}

.error {
    background: var(--error-background);
    border: var(--error-border);
    display: inline-block;
    font-size: var(--font-size-S);
    margin: 0.5em 0;
    padding: 0.75em 1.25em;
}